@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+SC:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin: 0;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#header {
    width: 100%; 
    height: 100vh; 
    --webkit-background-size: cover;
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: overlay;
    background: black;
    color: #85ecb9;
    scroll-behavior: smooth;
}



.header__row {
    font-family: 'Encode Sans SC', sans-serif;
    position: absolute; 
    top: 50%; 
    left: 50%; 
    -webkit-transform: translate(-50%, -50%); 
            transform: translate(-50%, -50%);
    text-align: center;
}

#header__h1 {
    font-size: 65px;
}

#header__h3 {
    padding-top: 15px;
    font-size: 35px; 
}

#profilepic {
    border-radius: 30px;
    margin-top: 10px; 
    margin-bottom: 5px;
}

.header__btn {
    font-family: 'Encode Sans SC', sans-serif;
    display: inline-block;
    padding: 15px 40px; 
    text-decoration: none;
    text-align: center; 
    background: #85ecb9; 
    font-size: 16px; 
    color: black; 
    border-radius: 50px;
    margin-top: 30px;
    cursor: pointer;
    opacity: 1;
}

.header__btn > a {
    text-decoration: none; 
    color: black;
}

.header__btn > a:hover{
    color: white;
    transition-duration: 0.5s;
}

.links__linkedin, .links__twitter, .links__github {
    display: inline-block;
    margin-top: 20px;
    margin-right: 25px;
}

.links__linkedin {
    margin-left: 20px;
}

.linkedin__anchor, .twitter__anchor, .github__anchor {
    color: #85ecb9;
}

@media screen and (max-width: 960px) {
    #header__h1 {
        font-size: 45px;
    }

    #header__h3 {
        padding-top: 15px;
        font-size: 25px; 
    }
}
#about {
    margin-top: 150px;
    background-color: black;
    color: white;
    padding-bottom: 20px;
}

.header__abt {
    font-family: 'Rubik', sans-serif;
    font-size: 45px;
    padding-top: 160px;
    padding-bottom: 10px;
}

.abt__hello {
    color: #85ecb9;
}

.abt__p1, .abt__p2, .abt__p3 {
    margin-top: 15px;
    font-family: 'Poppins', sans-serif;
    padding-left: 20px;
    padding-right: 20px; 
    padding-top: 10px;
}

.p1__job {
    text-decoration: underline;
    -webkit-text-decoration-color: #85ecb9;
            text-decoration-color: #85ecb9;
    -webkit-text-decoration-style: wavy;
            text-decoration-style: wavy;
}

.p1__company {
    text-decoration: underline;
    -webkit-text-decoration-color: #6ce9aa;
            text-decoration-color: #6ce9aa;
    -webkit-text-decoration-style: dotted;
            text-decoration-style: dotted;
}

.p1__stack, .p2__school, .p2__degree, .p3__mail {
    color: #6ce9aa;
}

.p3__mail {
    text-decoration: none;
}

.p1__stack:hover, .p2__school:hover, .p2__degree:hover, .p3__mail:hover {
    color: white;
    transition-duration: 0.5s;
}






.footer {
    padding-top: 30px;
    background-color: black;
    color: #85ecb9;
    font-family: 'Poppins', sans-serif;
    padding-bottom: 13px;
}

.footer__bottom {
    margin-top: 10px;
    margin-bottom: 10px; 
}

#bottomtotop {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #85ecb9;
    text-decoration: none;
}

.footer__bottom:hover {
    background-color: rgb(43, 43, 43);
}
